<template>
  <div>
      <ul>
          <li>
            <img src="../../assets/images/logicon01.png" alt="">
            <input type="text" name="" v-model="LUserPhone"  @blur="checkLPhone" placeholder="请输入手机号">
          </li>
          <li class="yzm">
            <img src="../../assets/images/logicon02.png" alt="">
            <input type="text" name="" @blur="checkLpicma" v-model="picLyanzhengma" placeholder="请输入验证码">
            <span id="mo">{{checkCode}}</span>
            <input type="button" id="code" @click="createCode" v-model="checkCode"/>
          </li>
          <li>
            <img src="../../assets/images/logicon03.png" alt="">
            <input type="text" placeholder="请输入短信验证码">
            <input type="button" value="获取验证码">
          </li>
      </ul>
      <div class="user_login">登录</div>
      <p>
        第一次登录即是注册<br>
        *我已阅读并同意 <a href="">《用户服务协议》</a>
      </p>

  </div>
</template>
<script>
  var code ;//在全局定义验证码
  export default {
    data () {
      return {
        LUserPhone:'',
        checkCode:'',
        picLyanzhengma:''
      }
    },
    methods:{
      // 验证登陆手机号格式
      checkLPhone(){
        this.$checkLPhone(this.LUserPhone);
      },
      // 图片验证码
      createCode(){
        this.checkCode = this.$createCode(this.checkCode);
      },
      // 失焦验证图和密码
      checkLpicma(){
        this.$checkLpicma(this.picLyanzhengma,this.checkCode);
      },
    },
    created(){
      this.checkCode = this.$createCode(this.checkCode);
    }
  }
</script>
<style lang="scss" scoped>
    ul{
      padding: 0 0.25rem;
      box-sizing: border-box;
      width: 100%;
      li{
        @include wh(100%,1.1rem);
        line-height: 1.1rem;
        border-bottom: 1px solid #{$borcolor};
        position: relative;
         &.yzm{
         background:url(#{$backimg}yzm.png);
         background-repeat: no-repeat;
         background-size: 1.47rem .69rem;
         background-position: 98% center;
        }
          span#mo{
            font-size: 0;
            line-height: 0;
          }
        img{
          width:percentage(42/700);
          height:.42rem;
          vertical-align:middle;
        }
        input[type="text"]{
          width:percentage(450/700);
          height:90%;
          font-size:.26rem;
          padding-left:.3rem;
          box-sizing: border-box;
          color:#{$fontcolor};
        }
        input[type="button"]{
          width:percentage(180/700);
          height:.7rem;
          background:#{$actcolor};
          color: #fff;
          border-radius: 0.5rem;
          font-size: 0.28rem;
          position: absolute;
          top: 0;
          bottom: 0;
          right: 0;
          margin: auto;
        }
        #code{
          font-weight: 600;
          font-size: .52rem;
          color: #000;
          background: -webkit-linear-gradient(left, #483cbd , #493a95, #52385c ,#6d384d);     /* 背景色渐变 */
          -webkit-background-clip: text;         /* 规定背景的划分区域 */
          -webkit-text-fill-color: transparent;  /* 防止字体颜色覆盖 */
        }
      }
    }
    .user_login{
      background: #406ff4;
      width: 5.9rem;
      height: .88rem;
      border-radius: 1rem;
      text-align: center;
      line-height: .88rem;
      color: #fff;
      font-size: 0.32rem;
      margin: .5rem auto;
    }
    p{
      text-align: center;
      color: #c0c3cc;
      font-size: 0.28rem;
      line-height: 0.5rem;
      a{
        color: #95affa;
      }
    }

</style>



